.App{ 
    width: 100vw;
    height: 100vh;
    display: flex;
    position:fixed;
    padding: 0;
    margin: 0;
    top: 0px;
    left:0px;
    flex-direction: column;
    justify-content: space-between;
    font-family: 'Helvetica', 'Arial', sans-serif;
    background-color: rgb(1, 103, 123);
}
.accessibility{
  border: 0; 
  clip: rect(0 0 0 0); 
  height: 1px; 
  margin: -1px; 
  overflow: hidden; 
  padding: 0; 
  position: absolute; 
  width: 1px; 
}
 /* width */
 ::-webkit-scrollbar {
    width: 10px;
  }
  
  /* Track */
  ::-webkit-scrollbar-track {
    background: rgb(1, 103, 123);
  }
  
  /* Handle */
  ::-webkit-scrollbar-thumb {
    background: white;
  }
  
  /* Handle on hover */
  ::-webkit-scrollbar-thumb:hover {
    background: rgb(0, 217, 255);
  } 
