.Room{
    background-color:rgb(1, 103, 123);   
    width: 100vw;
    height: 100vh;
    object-fit: cover;
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(4, 1fr);
    grid-template-areas: 
        "header header header header" 
        "users  input  input  input" 
        "chat   video  video  video" 
        "footer footer footer footer";
}
.header{
    grid-area: header; 
}
.videoInput{
    grid-area: input;
    padding: 0 2vw 2vh 2vw;
}
.video {
    grid-area: video;
    display: flex;
    justify-content: center;
    align-self: center;
    height: 72vh;
    padding: 0 2vw 2vh 2vw;
}
.chat{
    grid-area: chat;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    background-color: rgb(26, 25, 25);
    border-right: rgb(0, 0, 0) 1px solid;
    list-style: none;
    margin: 0;
    height:72vh;
    padding: 0 2vw 2vh 2vw;
}
.users{
    grid-area: users;
    display: flex;
    flex-direction: column; 
    justify-content: center;
    align-items: center;
    background-color: rgb(26, 25, 25);
    border: 1px solid black;
    padding: 0 2vw 0 2vw;
}
.footer{
    grid-area: footer;
    height: 8vh;
}
h1 {
    font-family: 'Helvetica', 'Arial', sans-serif;
    color: white;
    font-style: bold;
    margin: 1em;
} 

@media only screen and (max-width:900px){
    .Room{
        grid-template-columns: 1fr;
        grid-template-rows: repeat(5, 1fr);
        grid-template-areas: 
            "header " 
            "input  " 
            "video  " 
            "chat   " 
            "footer ";
    }
    .video{
        height:25vh;
    }
    .chat{
        height:40vh;
    }
}
.video-container {
    width: 72vw;
    height: 70vh;
