}
.event-flex-hidden{
    position: fixed;
    font-family: 'Helvetica', 'Arial', sans-serif;
    color: white;
    background-color: rgb(9, 219, 65);
    width: 100vw;
    height: 4vh;
    text-align: center;
    font-weight: bolder;
    font-size: large;
    padding: 1vh 2vw;
    z-index: 5;
    top:-8vh;
    transition: 500ms;
}

.event-flex-shown{
    position: fixed;
    font-family: 'Helvetica', 'Arial', sans-serif;
    color: white;
    background-color: rgb(9, 219, 65);
    width: 100vw;
    height: 4vh;
    text-align: center;
    font-weight: bolder;
    font-size: large;
    padding: 1vh 2vw;
    z-index: 5;
    top:0;
    transition: 100ms;
}
.footer-container {
    width: 96vw;
    height: 5vh;
    position: absolute;
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: white;
    background: rgb(0, 0, 0);
    padding: 1vh 2vw 1vh 2vw;
    bottom: 0px;
}
.footer-p{
    margin-top: 5px;
    margin-bottom: 0px;
    margin-right: 2vw;
    height: 1em;
    font-family: 'Helvetica', 'Arial', sans-serif;
}
.footer-right{
    margin-right: 0;
}

@media only screen and (max-width:900px) {
    .footer-container{
